<script>
    import * as API from '@/api/creditManagement'
    import { requestUrl } from '@/api/config'
    export default {
        data() {
            return {
                FILE_UPLOAD_URL: `${requestUrl}/credit/Loan/importExcel`,
                tableData: [],
                dataLogs: [],
                page: 1,
                total: 1,
                limit: 10,
                fileList: [],
                file: [],
                percentage: 0,
                loading: false,
                paramsUpload: {
                    "X-Token":localStorage.getItem('token')
                }
            }
        },
        mounted() {
            this.getDateLogs()
        },
        computed: {
            
        },
        methods: {
            initUpload() {
                this.file = null
                this.showProgress = false
                this.loadingProgress = 0
                this.tableData = []
                this.tableTitle = []
            },
            handleCurrentChange(){

            },
            uploadProgress(file){
                this.percentage = file.percent
            },
            uploadSuccess(res){
                this.loading = false
                if(res.code == 0){
                    this.tableData = res.data.data
                    this.total = res.data.succ_num
                }else{
                    this.$message.warning(res.msg)
                }
            },
            beforeUpload(){
                this.loading = true
            },
            getDateLogs(){
                API.creditHelpManagementLogs({}).then(res => {
                    this.dataLogs = res.data;
                    this.total = res.data.total;
                })
            },
            handleUploadFile() {
                // this.initUpload()
                /*API.creditHelpManagementUpload({}).then(res => {
                    this.tableData = res.data;
                })*/
            },
            handleRemove() {
                this.initUpload()
                this.$message.info('上传的文件已删除！')
            },
            handleBeforeUpload(file) {
                const fileExt = file.name.split('.').pop().toLocaleLowerCase()
                if (fileExt === 'xlsx' || fileExt === 'xls') {
                    this.readFile(file)
                    this.file = file
                } else {
                    this.$notify.warning({
                        title: '文件类型错误',
                        message: '文件：' + file.name + '不是EXCEL文件，请选择后缀为.xlsx或者.xls的EXCEL文件。'
                    })
                }
                return false
            },
            // 读取文件
            readFile(file) {
                const reader = new FileReader()
                reader.readAsArrayBuffer(file)
                reader.onloadstart = e => {
                    this.uploadLoading = true
                    this.tableLoading = true
                    this.showProgress = true
                }
                reader.onprogress = e => {
                    this.progressPercent = Math.round(e.loaded / e.total * 100)
                }
                reader.onerror = e => {
                    this.$message.error('文件读取出错')
                }
                reader.onload = e => {
                    this.$message.info('文件读取成功')
                    const data = e.target.result
                    const { header, results } = excel.read(data, 'array')
                    const tableTitle = header.map(item => { return { title: item, key: item } })
                    console.log(tableTitle)
                    this.tableData = results
                    this.tableTitle = tableTitle
                    this.uploadLoading = false
                    this.tableLoading = false
                    this.showRemoveFile = true
                }
            }
        }
